// import "./exchange-context"

export const hero = async () => {
  try {
    const response = await fetch(`${process.env.REACT_APP_STRAPI_URL}/heroes`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
    })
    return await response.json();
  } catch (error) {
    console.log(error);
  }
};
