import React, { useState } from "react";
import { AppBar, Icon, Grid, Menu, MenuItem, Button } from "@material-ui/core";
import logo from "../../img/logo-white.png";

export default function Header() {
  const [active, setActive] = useState(null);

  const handleClick = (event) => {
    console.log(event.currentTarget.childNodes[0].innerText);
    setActive(event.currentTarget.childNodes[0].innerText);
  };

  const handleClose = () => {
    setActive(null);
  };

  return (
    <AppBar position="sticky" id="main-navigation">
      <Grid container justify="space-between">
        <Grid item xs={4}>
          <img src={logo} id="logo" alt="logo" />
        </Grid>

        <Grid
          item
          container
          xs={6}
          justify="space-between"
          alignItems="flex-start">
          <Button
            onClick={handleClick}
            color="secondary"
            endIcon={<Icon>expand_more</Icon>}>
            Markets
          </Button>
          <Button
            onClick={handleClick}
            color="secondary"
            endIcon={<Icon>expand_more</Icon>}>
            About
          </Button>
          <Button color="secondary" endIcon={<Icon>expand_more</Icon>}>
            Communication
          </Button>
          <Button color="secondary" endIcon={<Icon>expand_more</Icon>}>
            Market Data
          </Button>
          <Button color="secondary" endIcon={<Icon>expand_more</Icon>}>
            Regulation
          </Button>
          <Menu
            id="other-menu"
            keepMounted
            open={active === "About"}
            onClose={handleClose}>
            <MenuItem onClick={handleClose}>5</MenuItem>
            <MenuItem onClick={handleClose}>6</MenuItem>
          </Menu>
        </Grid>
      </Grid>
    </AppBar>
  );
}
