import React, { useEffect, useState } from "react";
import { Grid, Typography, Button } from "@material-ui/core";
import { hero } from "../../../context/cms";
import { AbacusSvg as Abacus } from "../abacus";
import Tiles from "./cards";

export default function Intro() {
  const [heroContent, setHeroContent] = useState<any>({});

  // on load
  useEffect(() => {
    // an example way to init page data
    const init = async () => {
      let h: any = await hero();
      h = h.pop(); // response comes back in a single array so we pop the object out
      setHeroContent(h); // we save the object to the pages state
    };
    init();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // on state update
  useEffect(() => {
    // we can display that setHeroContent data once it has been udpdated in the pages state and use it below on lines 74 and 75
    console.log("title", heroContent.title);
    console.log("subTitle", heroContent.title);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [heroContent]);

  return (
    <Grid container id="intro" justify="space-between" alignItems="flex-start">
      <Grid
        item
        container
        xs={5}
        id="intro-typography"
        justify="flex-end"
        alignItems="flex-start"
        direction="column">
        <Typography variant="h4">Commodities made</Typography>
        <Typography variant="h1">Simple</Typography>
        <div className="theme-underline"></div>
        <Typography variant="h5">
          Velocity and Security in Commodity Trading Markets.
        </Typography>

        <Button variant="contained" color="primary">
          Learn More
        </Button>
      </Grid>
      <Grid item container xs={7} justify="flex-end">
        <Abacus />
      </Grid>
      <Grid item container xs={12}>
        <Typography variant="h4">At A Glance</Typography>
      </Grid>

      <Tiles />
    </Grid>
  );
}
