import React from "react";
import { Grid, Typography } from "@material-ui/core";
import teamList from "./data";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import TwitterIcon from "@material-ui/icons/Twitter";

type TeamProps = {
  src: string;
  name: string;
  title: string;
  children?: any;
};

function TeamMember(props: TeamProps) {
  const { src, title, name } = props;
  return (
    <Grid
      item
      container
      xs={4}
      justify="center"
      alignItems="center"
      direction="column"
      className="headshot">
      <Grid className="headshot-content">
        <img src={src} alt="team" style={{width: "120px"}}/>
        <div className="theme-underline"></div>

        <Typography variant="h6" gutterBottom>
          {name}
        </Typography>

        <Typography variant="h6" color="primary" gutterBottom>
          {title}
        </Typography>

        <Grid>
          <LinkedInIcon />
          <TwitterIcon />
        </Grid>
        {props.children}
      </Grid>
    </Grid>
  );
}

export default function Team() {
  return (
    <Grid container id="team">
      <Grid item container justify="center" alignItems="center">
        <Typography variant="h4" color="secondary" gutterBottom>
          Our Team
        </Typography>
      </Grid>

      <Grid
        item
        container
        spacing={10}
        justify="flex-start"
        alignItems="center"
        id="headshot-container">
        {teamList.map((obj, index) => {
          return (
            <TeamMember
              key={index}
              name={obj.name}
              title={obj.title}
              src={obj.src}
            />
          );
        })}
      </Grid>
    </Grid>
  );
}
